.main {
    height: 50px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    color: white;
}

.navbar {
    background: transparent;
    transition: .3s ease;
}

.navbar.active {
    background: #141414;
}

.logo,
.contact_number {
    width: 20%;
}

.logo,
.contact_number span,
.menu_list ul li a {
    color: rgb(231, 231, 231) !important;
    font-family: 'Montserrat', sans-serif;
    margin: 0 10px;
    font-weight: 400;
    font-size: 13px;
}

.menu_list ul {
    margin: 0;
}

@media screen and (max-width:478px) {

    .logo,
    .contact_number {
        width: auto;
    }
}