.main_bg_cover {
    background-image: linear-gradient(to top, rgba(0, 0, 0, 0.7) 0%, rgba(0, 0, 0, 0.7) 100%), url(https://ik.imagekit.io/workcations/gallery/triplist/main-cover.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
    position: relative;
    overflow: hidden;
}

.hero__section {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: 1px solid red;
}

.search__field {
    width: 45vw;
    height: 4vw;
    border: 1px solid rgb(10, 158, 136);
    border-radius: 5px;
    padding: 8px;
    font-size: 1.5vw;
    font-weight: 400;
    color: white;
    background: rgba(255, 255, 255, 0.2);
    position: relative;
}

.search__location {
    position: relative;
}

.search__location i {
    position: absolute;
    top: 0;
    right: 0;
    border-radius: 0 5px 5px 0;
    padding: 19px;
    background-color: rgb(10, 158, 136);
    color: #fff;
}

.site__review {
    display: flex;
    margin-top: 10px;
}

.img__review {
    padding: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}

.img__review img {
    width: 100px;
}

.img__review span {
    font-size: 12px;
}

@media screen and (max-width:478px) {
    .main_bg_cover {
        background-position: center;
        padding: 0 30px;
    }

    .search__field {
        width: 80vw;
        height: 10vw;
        font-size: 3vw;
        font-weight: 400;
    }

    .search__location i {
        padding: 12px;
    }

