* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style: none !important;
    text-decoration: none !important;
}

.hero__heading {
    font-family: 'Montserrat', sans-serif;
    font-size: 3.5vw;
    font-weight: 600;
    margin-bottom: 2vh;
}

.sub__heading {
    font-family: 'Montserrat', sans-serif;
    font-size: 2vw;
    font-weight: 600;
    margin-bottom: 10vh;
}


@media screen and (max-width:478px) {
    .hero__heading {
        font-size: 8vw;
    }

    .sub__heading {
        font-size: 4vw;
    }
}