.sideMenu {
    position: absolute;
    top: 60px;
    right: 0;
    width: 280px;
    height: 100vh;
    background-color: #fff;
    display: flex;
    justify-content: flex-start;
    padding: 10px 0;
    transform: translateX(100%);
    transition: all 0.4s;
}

.sideMenu__open {
    transform: translateX(0%);
}

.sideMenu ul {
    padding: 0;
    margin: 0;
}

.sideMenu li {
    padding: 1.5rem 4rem;
    border-bottom: 1px solid rgb(0, 0, 0, 0.2);
}

.sideMenu li a {
    color: #333333;
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
    font-size: 16px;
}

.sideMenu a:hover {
    color: orange;
}

/* backdrop  */
.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.5);
    display: none;
}

.backdrop__open {
    display: block;
}